body {
  color: #f0f0f5;
  background-color: #1e1e2f;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header {
  text-align: center;
}

#logo {
  max-width: 30%;
  height: auto;
}

h1 {
  text-align: center;
  color: #fff;
  margin-top: 30px;
  font-size: 2.5rem;
}

#mostrarCampeonesBtn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px;
  padding: 15px 30px;
  font-size: 1.2rem;
  transition: background-color .3s;
}

#mostrarCampeonesBtn:hover {
  background-color: #0056b3;
}

.champion-container {
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 20px;
  width: 80%;
  margin: 20px 0;
  display: grid;
}

.champion {
  text-align: center;
  background-color: #2a2a3a;
  border-radius: 10px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #0003;
}

.champion:hover {
  filter: brightness(.9);
  transform: translateY(-10px);
  box-shadow: 0 8px 16px #0000004d;
}

.champion img {
  object-fit: cover;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}

.champion p {
  color: #fff;
  margin: 0;
  font-size: 1rem;
}

@media (width <= 768px) {
  .champion-container {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }

  #mostrarCampeonesBtn {
    padding: 10px 20px;
    font-size: 1rem;
  }

  h1 {
    font-size: 2rem;
  }
}

.additional-info {
  color: #fff;
  text-align: left;
  background-color: #000000b3;
  border-radius: 10px;
  padding: 20px;
  display: none;
  position: absolute;
  inset: 0;
}

.champion:hover .additional-info {
  display: block;
}

#imagen-grande {
  z-index: 999;
  background: #000000b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
  overflow: auto;
}

.imagen-grande-container {
  background: #000000e6;
  border-radius: 10px;
  max-width: 90%;
  max-height: 90%;
  padding: 20px;
  display: flex;
  overflow-y: auto;
}

.imagen-grande-container img {
  border-radius: 8px;
  width: 300px;
  height: auto;
  margin-right: 20px;
}

.imagen-grande-texto {
  color: #f0f0f5;
  max-width: 60%;
}

#cerrar-imagen-grande {
  cursor: pointer;
  color: #fff;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.abilities {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

.ability {
  text-align: center;
  flex: 1 0 100px;
  margin: 10px;
}

.ability img {
  border-radius: 4px;
  width: 50px;
  height: auto;
}

.ability p {
  margin: 5px 0 0;
  font-size: .9rem;
}

h2, h3, h4 {
  color: #fff;
}

h3 {
  margin-top: 20px;
}
